const orderSnackbar = document.getElementById("snackbar-form-submit");
// Корзина
const orderList = document.querySelector('.order-info__list')
let productArray = [];
const generateOrderProduct = (img, title, price, id) => {
	return `
		<li class="order-info__item">
			<article class="o-item" data-id="${id}">
				<img src="${img}" alt="${title}" class="o-item__image">
				<h4 class="o-item__title">${title}</h4>
				<span class="o-item__price">${normalPrice(price)}</span>
				<button class="o-item__delete btn-reset" aria-label="Удалить товар">
					×
				</button>
			</article>
		</li>
	`;
};

function cartOrderItems() {
	let listItemsArray = cartProductsList.children;
	let fullprice = fullPrice.textContent;
	let length = listItemsArray.length;
	document.querySelector('.order-info__summ').textContent = `${fullprice}`;
	if (length == 0) {
		document.querySelector('.order-info__quantity-numb').textContent = `нет товаров`;
	} else if (length == 1) {
		document.querySelector('.order-info__quantity-numb').textContent = `${length} товар`;
	} else if (length < 5 && length > 1) {
		document.querySelector('.order-info__quantity-numb').textContent = `${length} товара`;
	} else if (length >= 5) {
		document.querySelector('.order-info__quantity-numb').textContent = `${length} товаров`;
	}
	for (item of listItemsArray) {
		let img = item.querySelector('.c-item__image').getAttribute('src');
		let priceString = priceWithoutSpaces(item.querySelector('.c-item__price').textContent);
		let title = item.querySelector('.c-item__title').textContent;
		let id = item.querySelector(`.c-item`).dataset.id;
		orderList.insertAdjacentHTML('afterbegin', generateOrderProduct(img, title, priceString, id));

		let obj = {};
		obj.title = title;
		obj.price = priceString;
		productArray.push(obj)
	}
}
cartOrderItems()
const updateOrderItems = () => {
	let listItemsArray = cartProductsList.children;
	let fullprice = fullPrice.textContent;
	let length = listItemsArray.length;
	document.querySelector('.order-info__summ').textContent = `${fullprice}`;
	if (length == 0) {
		document.querySelector('.order-info__quantity-numb').textContent = `нет товаров`;
	} else if (length == 1) {
		document.querySelector('.order-info__quantity-numb').textContent = `${length} товар`;
	} else if (length < 5 && length > 1) {
		document.querySelector('.order-info__quantity-numb').textContent = `${length} товара`;
	} else if (length >= 5) {
		document.querySelector('.order-info__quantity-numb').textContent = `${length} товаров`;
	}

}
document.querySelector('.order-info__list').addEventListener('click', (e) => {
	if (e.target.classList.contains('o-item__delete')) {
		let id = e.target.closest('.o-item').dataset.id;
		let cartProduct = document.querySelector(`.c-item[data-id='${id}']`).closest('li');
		deleteProducts(cartProduct);
		e.target.closest('.o-item').remove();
		updateOrderItems()
	}
})


