// Валидация форм
function ValidMail() {
	let re = /^[\w-\.]+@[\w-]+\.[a-z]{2,4}$/i;
	let myMail = document.querySelector('.cf-top__input-mail').value;
	let valid = re.test(myMail);
	if (valid) {
		output = 'Email введен правильно!';
		console.log(output)
	}
	else {
		output = 'Email введен неправильно!';
		console.log(output)

		orderSnackbar.textContent = 'Неправильно введен Email! Проверьте ваши данные!';
		// Добавить "show" класс для DIV
		orderSnackbar.className = "show--red";
		setTimeout(function () { orderSnackbar.className = orderSnackbar.className.replace("show--red", ""); }, 5500);

	}
	return valid;
}

function ValidPhone() {
	let re = /^\d[\d\(\)\ -]{7,14}\d$/;
	let myPhone = document.querySelector('.cf-top__input--tel').value;
	let valid = re.test(myPhone);
	if (valid) {
		output = 'Номер телефона введен правильно!';
		console.log(output)
	}
	else {
		output = 'Номер телефона  введен неправильно!';
		console.log(output)

		orderSnackbar.textContent = 'Неправильно введен номер телефона! Проверьте ваши данные!';
		// Добавить "show" класс для DIV
		orderSnackbar.className = "show--red";
		// После 3 секунд, извлеките класс show из DIV
		setTimeout(function () { orderSnackbar.className = orderSnackbar.className.replace("show--red", ""); }, 5500);

	}
	return valid;
}
