// Отправка формы на почту
document.querySelector('.order__form').addEventListener('submit', (e) => {
	e.preventDefault();
	let self = e.currentTarget;

	let formData = new FormData(self);
	let name = self.querySelector('[name="Имя"]').value;
	let surname = self.querySelector('[name="Фамилия"]').value;
	let tel = self.querySelector('[name="Номер телефона"]').value;
	let mail = self.querySelector('[name="E-mail"]').value;
	let payment = self.querySelector('[name="Способ оплаты"]').value;
	let delivery = self.querySelector('[name="Способ получения"]').value;
	let city = self.querySelector('[name="Город"]').value;
	let deliveryAddress = self.querySelector('[name="Адрес доставки"]').value;

	formData.append('Товары', JSON.stringify(productArray));
	formData.append('Имя', name);
	formData.append('Фамилия', surname);
	formData.append('Номер телефона', tel);
	formData.append('E-mail', mail);
	formData.append('Способ оплаты', payment);
	formData.append('Способ получения', delivery);
	formData.append('Город', city);
	formData.append('Адрес доставки', deliveryAddress);

	let xhr = new XMLHttpRequest();


	xhr.onreadystatechange = function () {
		if (xhr.readyState === 4) {
			if (xhr.status === 200) {
				console.log('Отправлено');
				orderSnackbar.textContent = 'Заказ оформлен! В скором времени с вами свяжется наш оператор!';
				// Добавить "show" класс для DIV
				orderSnackbar.className = "show";
				// После 3 секунд, извлеките класс show из DIV
				setTimeout(function () { orderSnackbar.className = orderSnackbar.className.replace("show", ""); }, 5500);
			} else {
				orderSnackbar.textContent = 'Вы все сделали правильно! Извините, сервер недоступен!';
				// Добавить "show" класс для DIV
				orderSnackbar.className = "show--red";
				// После 3 секунд, извлеките класс show из DIV
				setTimeout(function () { orderSnackbar.className = orderSnackbar.className.replace("show--red", ""); }, 5500);
			}
		}
	}

	xhr.open('POST', '/resources/mail.php', true);
	if (ValidPhone() && ValidMail()) {
		if (localStorage.getItem('products') !== null) {
			xhr.send(formData);
			self.reset();
		} else {
			orderSnackbar.textContent = 'Корзина пуста! Сначала требуется добавить товар!';
			// Добавить "show" класс для DIV
			orderSnackbar.className = "show--red";
			// После 3 секунд, извлеките класс show из DIV
			setTimeout(function () { orderSnackbar.className = orderSnackbar.className.replace("show--red", ""); }, 5500);
		}
	}
});
